/*---------slider-----------------*/
.slideshow-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.slideshow {
  height: 220px;
  width: 100%; /* or whatever width you prefer */
  margin-bottom: 20px;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  position: relative; /* add relative positioning */
}

.slideshow > .mover {
  top: 0;
  left: 0;
  height: 100%;
  transform: translate3d(0, 0, 0);
}

#table > .mover, #chair > .mover, #bed > .mover {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  animation: moveSlideshow 15s linear infinite;
}

.slideshow:hover .mover {
  animation-play-state: paused;
}

#table .mover {
  animation-name: moveSlideshowTable;
}

@keyframes moveSlideshowTable {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

#chair .mover {
  animation-name: moveSlideshowChair;
}

@keyframes moveSlideshowChair {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

#bed .mover {
  animation-name: moveSlideshowBed;
}

@keyframes moveSlideshowBed {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}


