  }
}

/* 6. Subscribe
/*----------------------------------------------*/
.subscribe-content p {
  color: var(--gray-color-500);
}

@media only screen and (max-width: 1199px) {
  .subscription-form input.form-control,
  .subscription-form button {
    width: 100%;
    margin-bottom: 10px;
  }
}

/* 7. Instagram
/*----------------------------------------------*/
#instagram figure.instagram-item {
  width: 20%;
}
figure.instagram-item img.insta-image {
  width: 90%;
  height: 90%;
  object-fit: cover;
}
.instagram-item .icon-overlay {
  top: 30px;
  left: 0;
  width: 100%;
  height: auto;
  color: var(--light-color);
  opacity: 0;
  transition: 0.9s ease-out;
}
.instagram-item:hover .icon-overlay {
  top: 0;
  opacity: 1;
}
.instagram-item .icon-overlay svg.instagram {
  height: 50px;
}

@media only screen and (max-width: 767px) {
  #instagram figure.instagram-item {
    width: 30%;
  }
}

@media only screen and (max-width: 501px) {
  #instagram figure.instagram-item {
    width: 100%;
  }
}

/* - Shop Page
--------------------------------------------------------------*/
/* ----------- Search Bar -----------*/
.shopify-grid select#input-sort {
  font-size: 1.2em;
  color: var(--dark-grey-color);
  border: none;
  padding: 0;
  text-align: center;
}

/* ----------- SideBar -----------*/
.sidebar input.search-field {
  width: 290px;
  height: 50px;
  box-shadow: 5px 5px 15px 5px #eee;
  border: 1px solid #eee;
  padding-left: 20px;
  outline-offset: 0;
}
.sidebar .search-icon {
  padding: 14px;
}
.sidebar svg.search {
  width: 20px;
  height: 20px;
}
.sidebar h5.widget-title {
  font-size: 1.4em;
  margin-bottom: 15px;
}
.sidebar .sidebar-list li {
  font-size: 1.2em;
}
.sidebar .sidebar-list li a {
  color: var(--dark-30);
}
.sidebar .sidebar-list li a:hover {
  color: var(--primary-color);
